import axios from 'axios'

const API_URL = 'https://darling-cucurucho-3b33c4.netlify.app';

export const test = test_text => {
    return axios
        .post(`${API_URL}/test`, {
            text: test_text.text
        })
        .then(response => {
            console.log(response.data)
            return response.data
        })
}
